/** @format */

.Page_Profile {
  /* background-color: #f6f9fb; */
  display: grid;
  grid-template-columns: 94% 6%;
  margin: 30px auto;
  align-items: flex-start;
  border-bottom: solid 1px rgba(36, 39, 44, 0.12);
  width: 100%;
}

.Page_Profile:last-child {
  border: none;
}

.imgContainer {
  background: #ffffff;
  border: 1px solid rgba(36, 39, 44, 0.1);
  border-radius: 8px;
  width: 35px;
  align-items: center;
}

.PageImg {
  width: 100%;
  border-radius: 3px;
}

.Page_Profile > label {
  display: grid;
  grid-template-columns: 4% 96%;
}

.Page_label {
  top: 0;
  transform: none;
  position: relative;
}
.Page_label div {
  margin-top: 0;
}

.Page_Profile h1 {
  font-family: Roboto;
  font-size: 18px;
  font-weight: 500;
  color: #24272c;
  align-self: center;
}

.Page_Profile input {
  width: 22px;
  height: 25px;
}

.alreadyUsed {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #f7b500;
}
