/** @format */

.Loader_Container {
  padding: 0;
  margin: 0;
  max-width: 100%;
  width: 100%;
  height: 100vh;
  background: #fff;
  z-index: 11111;
  position: absolute;
  top: 0;
}

.lds-ripple {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  position: relative;
  width: 90px;
  height: 90px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #12b0ee;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}

