.Modal2 {
  font-family: Roboto, "sans serif";
  display: grid;
  margin-top: 1%;
  grid-template-columns: 13% 87%;
  align-items: baseline;
}

.Modal_Title h1 {
  color: #24272c;
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 5%;
}

.Header_Profile {
  margin-top: 4%;
  margin-left: 15%;
  display: flex;
  align-items: center;
}

.Business_Img {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  border-radius: 50%;
}

.Header_Profile p {
  opacity: 0.5;
  font-family: Roboto;
  font-size: 12px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  color: #3b414a;
  margin-left: 1%;
}

.Business_Img img {
  width: 100%;
  height: auto;
}
