/** @format */

.inputComponent {
  padding-top: 10px;
  padding-left: 20px;
  width: 100%;
  margin: auto;
  height: 52px;
  border-radius: 8px;
  background-color: #f2f3f7;
  border: none;
  outline: none;
  font-family: Roboto;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4c5666;
}

.labelComponent {
  transform: translate(10%, -50%);
  position: absolute;
  top: 50%;
  left: 5%;
  text-align: left;
  color: #7b8aa2;
  transition: 0.2s ease-in-out;
  pointer-events: none;
}

.inputComponent:focus + .labelComponent,
.inputComponent:valid + .labelComponent,
.inputComponent:read-only + .labelComponent {
  left: calc(20px - 1.5%);
  top: 25%;
  font-size: 10px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}

input[type="file"] {
  background-color: transparent;
  opacity: 0;
  border-radius: 5px;
  margin: 0;
  width: 100%;
  position: absolute;
  top: 0;
  height: 100%;
  padding: 0;
}

input[type="file"]::-webkit-file-upload-button {
  display: none;
}

input[type="file"] + .labelComponent {
  display: none;
}

.fileupload {
  width: 40%;
  padding: 4px;
  position: relative;
  background-color: rgb(242, 243, 247);
}

.fileupload-2 {
  width: 50%;

  padding: 20px 15px 20px 15px;
  margin: 10px;
}

.fileupload p {
  font-size: 12px;
  text-align: center;
  color: rgb(97, 108, 125);
}

.fileupload h1 {
  font-size: 14px;
  text-align: center;
  color: rgb(97, 108, 125);
  margin: 10px 0 5px 0;
}

.dropdown {
  background-color: #f2f3f7;
  padding: 19px 20px 0px 20px;
  position: absolute;
  top: 25%;
  width: 100%;
  overflow-y: scroll;
  height: fit-content;
  max-height: 300px;
  z-index: 111;
}

.dropdown ul {
  list-style: none;
  margin: 20px 10px 0 10px;
}

.dropdown ul li {
  margin-bottom: 30px;
  position: relative;
  text-align: left;
}

.dropdown ul li:after {
  content: "";
  position: absolute;
  bottom: -25%;
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.609);
  left: 0;
}
.hide {
  height: 0 !important;
  padding: 0 !important;
}
