/** @format */

.Logo_Section {
  padding: 10px;
  width: 100%;
  text-align: center;
  background-color: #f3f7f9;
}

.Logo_Section svg {
  width: 35%;
  margin: auto;
}

main.Form_Section {
  background-color: #fff;
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  box-shadow: 0 -4px 5px 0 rgb(0 0 0 / 8%);
  height: 100%;
}

.Form_Header {
  padding-top: 32px;
  display: grid;
  grid-template-columns: 15% 80%;
}

.Form_Header .Back_Btn {
  align-self: center;
  text-align: center;
}

.Form_Header .Header_Text h1 {
  text-align: start;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
  letter-spacing: normal;
  color: #2e3239;
}
